.WindowInput {
    background-color: white;
    margin-top: 20px;
    padding: 25px;
    position: relative;
}

.WindowInput--remove {
    position: absolute;
    top: 0;
    right: 0;
    margin: 20px;
    cursor: pointer;
    opacity: 0.7;
}

.WindowInput--remove:hover {
    opacity: 1;
}
